import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import {AboutSection, Avatar, Title, Text, SubTitle} from './style';
import {SectionIntro, ContainerLayout, ResumeButton, ButtonDefault} from "../common";
import { GitHub, Linkedin, Mail } from 'react-feather'

const About = () => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "cv.png" }) {
        childImageSharp {
          fluid(maxWidth: 550) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <>
      <SectionIntro>
        <ContainerLayout>
          <AboutSection>
            <div>
                <Avatar fluid={data.placeholderImage.childImageSharp.fluid} alt="user photo" />
                <SubTitle>Software Engineer</SubTitle>
                <a href="https://github.com/BioLe" style={{marginLeft:'40%'}}><GitHub /> </a>
                <a href="https://www.linkedin.com/in/leonardmelo/" style={{marginLeft:'10%'}}><Linkedin /> </a>
            </div> 
            <div>
                <Title> Hello, Iâ€™m Leonardo </Title>
                <Text> I'm a software engineer from Portugal <u>living in Lisbon</u>, 98% way from having a masters in computer science from <a href="https://isel.pt/en/">ISEL</a>.
                    I have a passion for <u>Backend Development</u>, with a special interest in <u>dashboards and geographical data</u>.
                    I strive to always be learning and keeping up with the latest technologies, even if it seems an impossible task sometimes.
                    You can see what I've been up to in the <a href="/works">Projects</a> section!</Text>
                    
                    <Text>Outside of work I enjoy practicing Archery ðŸŽ¯ and lately I've been diving into the world of DIY and home renovations ðŸ‘·! In relaxing days, I like to spend time cooking and watching movies.</Text>
                {/* <ResumeButton href="CV_Leonardo_Melo.pdf" target="_blank">Download CV</ResumeButton> */}
                <div >
                    <ButtonDefault href={`mailto:leonardo.melo@outlook.pt`} style={{left:'25%'}}> Lets chat </ButtonDefault>
                </div>
            </div>
          </AboutSection>
        </ContainerLayout>
      </SectionIntro>
    </>
  )
}


export default About
